.entries-container {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
    max-width: 1200px;
    margin: 4rem auto 0;
    padding: 0 2rem;
  }
  
  .entry {
    background-color: rgba(255, 255, 255, 0.8);
    border-radius: 12px;
    overflow: hidden;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    transition: transform 0.3s ease, box-shadow 0.3s ease;
  }
  
  .entry:hover {
    transform: translateY(-5px);
    box-shadow: 0 6px 12px rgba(0, 0, 0, 0.15);
  }
  
  .entry-link {
    display: flex;
    flex-direction: column;
    height: 100%;
    text-decoration: none;
    color: inherit;
  }
  
  .entry-image {
    width: 100%;
    height: 0;
    padding-bottom: 75%; /* 4:3 aspect ratio */
    position: relative;
    overflow: hidden;
  }
  
  .entry-image img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
  
  .entry-content {
    padding: 1.5rem;
  }
  
  .entry-content h3 {
    margin: 0 0 0.5rem;
    font-size: 1.5rem;
    color: #333;
  }
  
  .entry-content p {
    margin: 0;
    font-size: 1rem;
    color: #666;
  }
  
  /* Make every third card span two rows */
  .entry:nth-child(3n) {
    grid-row: span 2;
  }
  
  /* Responsive design */
  @media (max-width: 768px) {
    .entries-container {
      grid-template-columns: 1fr;
    }
    
    .entry:nth-child(3n) {
      grid-row: auto;
    }
  }